html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Rubik, sans-serif;
  background-color: hsl(226deg, 43%, 10%);
  color: hsl(236deg, 68%, 95%);
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 71.875em) {
  body {
    height: 100vh;
  }
}

.container {
  margin: 3.125rem auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.25rem;
  width: 72vw;
}
@media (min-width: 40em) {
  .container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media (min-width: 71.875em) {
  .container {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}

.loader {
  margin: 0 auto;
}
@media (min-width: 40em) {
  .loader {
    grid-row: 1/2;
    grid-column: 3/4;
  }
}

@media (min-width: 40em) {
  .main {
    grid-row: 1/3;
    grid-column: 1/2;
  }
}
h1 {
  font-size: 1.875rem;
  font-weight: 300;
  margin: 0;
  padding-bottom: 2.5rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 71.875em) {
  h1 {
    margin-bottom: 0.625rem;
  }
}

h3 {
  font-weight: 500;
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
  margin: 0.6875rem 0;
}

.card {
  background: hsl(235deg, 46%, 20%);
  padding: 1.25rem;
  border-radius: 0.625rem;
}
.card__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: -0.9375rem;
}
@media (min-width: 40em) {
  .card__header {
    margin-bottom: 0;
  }
}
.card__ellipsis:hover {
  filter: brightness(150%);
  cursor: pointer;
}
.card__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .card__info {
    flex-direction: column;
    align-items: flex-start;
  }
}
.card__title {
  font-size: 1.375rem;
}
.card__time {
  font-size: 2.5rem;
  margin: 0;
  font-weight: 300;
}
@media (min-width: 40em) {
  .card__time {
    font-size: 3.75rem;
  }
}
.card__last_week {
  color: hsl(235deg, 45%, 61%);
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .card__last_week {
    font-size: 1rem;
  }
}

.card_image--work {
  background: url("../../../images/icon-work.svg") hsl(15deg, 100%, 70%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.card_image--play {
  background: url("../../../images/icon-play.svg") hsl(195deg, 74%, 62%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.card_image--study {
  background: url("../../../images/icon-study.svg") hsl(348deg, 100%, 68%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.card_image--exercise {
  background: url("../../../images/icon-exercise.svg") hsl(145deg, 58%, 55%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.card_image--social {
  background: url("../../../images/icon-social.svg") hsl(264deg, 64%, 52%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.card_image--selfcare {
  background: url("../../../images/icon-selfcare.svg") hsl(43deg, 84%, 65%) no-repeat 90% -5px;
  height: 95px;
  margin-bottom: -50px;
  border-radius: 0.625rem;
}

.person {
  background-color: hsl(246deg, 80%, 60%);
  padding: 1rem 2.5rem 1.25rem 1rem;
  margin-bottom: -1.25rem;
  border-radius: 0.625rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .person {
    flex-direction: column;
    align-items: flex-start;
    padding: 2.5rem 2.5rem 0.625rem 1.25rem;
  }
}
.person__image {
  width: 5.625rem;
  height: 5.625rem;
  border: solid 0.1875rem white;
  border-radius: 50%;
}
.person__subtext {
  font-size: 1rem;
  color: hsl(236deg, 100%, 87%);
  font-weight: 300;
  margin: 1.875rem 0 0 0;
}

.nav {
  background: hsl(235deg, 46%, 20%);
  border-radius: 0.625rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 1.875rem 1.25rem 0.625rem 2.5rem;
}
@media (min-width: 40em) {
  .nav {
    flex-direction: column;
    justify-content: flex-end;
    padding: 2.1875rem 1.25rem 0.9375rem 2.5rem;
  }
}
.nav__item {
  font-size: 1.375rem;
  color: hsl(235deg, 45%, 61%);
  cursor: pointer;
}
.nav__item:hover, .nav__item:active {
  color: hsl(236deg, 68%, 95%);
}
.nav .active {
  color: hsl(236deg, 68%, 95%);
}

.loader {
  width: 9.375rem;
  height: 9.375rem;
  border: 0.3125rem solid #FFF;
  border-bottom-color: #FF3D00;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.error-text {
  text-align: center;
  background-color: #d41a1a;
  color: white;
  font-size: 1.125rem;
  padding: 0.625rem;
  border-radius: 0.25rem;
}