@use '../util' as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,*::after,*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(font-family), sans-serif;
  background-color: var(colors-background-body);
  color: var(colors-text-light);
}

